import { getMySNCardList } from '@/api/user'
import nodata from '@/components/noData'
export default {
  name: 'ucGuaranteed',
  data () {
    return {
      curId: [], // 当前展开项
      cardData: [],
      noDataShow: false,
      noData: {
        txt: '还没有延保凭证呢~',
        img: 'static/images/noOrder.png'
      }
    }
  },
  created () {
    this.$vux.loading.show()
    getMySNCardList({})
      .then(res => {
        this.$vux.loading.hide()
        if (res.data.errorcode === 0) {
          // 处理数据
          this.cardData = res.data.data.snlist
          if (res.data.data.snlist.length < 1) {
            this.noDataShow = true
          }
        } else {
          this.noDataShow = true
          this.$vux.toast.text(res.data.errormsg, 'middle')
        }
      })
      .catch(err => {
        this.$vux.loading.hide()
        this.noDataShow = true
        console.log(err)
        this.$vux.toast.text('网络异常！', 'middle')
      })
  },
  components: {
    nodata
  },
  methods: {
    tipsToggle (i) {
      if (this.curId.indexOf(i) !== -1) {
        //
        this.curId.splice(this.curId.indexOf(i), 1)
      } else {
        this.curId.push(i)
      }
    }
  }
}



